.working-day-list-item {
  &-date {
    max-width: 150px;
    width: 150px;
    height: 40px;
    display: flex;
    align-items: center;

    span {
      font-size: 16px;
      font-weight: 500;
      font-family: "sans-serif";
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &-description {
    margin-left: 15px;
    max-width: 800px;
    width: 800px;

    span {
      font-size: 14px;
      font-weight: 400;
      font-family: "sans-serif";
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      -webkit-line-clamp: 2;
      overflow: hidden;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }
    p {
      -webkit-line-clamp: 2;
      overflow: hidden;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }
  }

  &-number-container {
    display: flex;
    align-items: center;
    margin-left: 2px;
  }

  &-icon {
    margin-left: 30px;
    background-color: #eae8e8;
    padding: 4px;
    border-radius: 10px;
  }
}
