.usage-of-machine-list-item {
  &-date {
    max-width: 250px;
    width: 250px;
    height: 40px;
    display: flex;
    align-items: center;

    span {
      font-size: 16px;
      font-weight: 500;
      font-family: "sans-serif";
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    &.header {
      span {
        font-weight: 800;
      }
    }
  }

  &-description {
    margin-left: 15px;
    max-width: 800px;
    width: 800px;

    p {
      -webkit-line-clamp: 1;
      overflow: hidden;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }

    &.header {
      p {
        font-weight: 800;
        color: black;
      }
    }
  }

  &-number-container {
    width: 200px;
    max-width: 200px;
    display: flex;
    align-items: center;
    margin-left: 15px;

    &.header {
      p {
        font-weight: 800;
        color: black;
      }
    }
  }
}
